import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';

const About = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './about')
);
const Education = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './education')
);
const Experience = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './experience')
);
const Links = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './education')
);
const Contact = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './contact')
);

const Profile = ({ match }) => {
  console.log(match.url);
  return (
    <Suspense fallback={<div className="loading" />}>
      <Switch>
        <Route
          path={`${match.url}/about`}
          render={(props) => <About {...props} />}
        />
        <Route
          path={`${match.url}/education`}
          render={(props) => <Education {...props} />}
        />
        <Route
          path={`${match.url}/experience`}
          render={(props) => <Experience {...props} />}
        />
        <Route
          path={`${match.url}/links`}
          render={(props) => <Links {...props} />}
        />
        <Route
          exact
          path={`${match.url}/contact`}
          render={(props) => <Contact {...props} />}
        />

        {/* <Redirect to="/error" message="from profile" /> */}
      </Switch>
    </Suspense>
  );
};
export default Profile;
